@font-face {
  font-family: 'fontello';
  src: url('../font/fontello/font/fontello.eot?97132756');
  src: url('../font/fontello/font/fontello.eot?97132756#iefix') format('embedded-opentype'),
       url('../font/fontello/font/fontello.woff2?97132756') format('woff2'),
       url('../font/fontello/font/fontello.woff?97132756') format('woff'),
       url('../font/fontello/font/fontello.ttf?97132756') format('truetype'),
       url('../font/fontello/font/fontello.svg?97132756#fontello') format('svg');
  font-weight: normal;
  font-style: normal;
}
/* Chrome hack: SVG is rendered more smooth in Windozze. 100% magic, uncomment if you need it. */
